import React from 'react'
import { connect } from 'react-redux'

import Nav from '../Nav/Nav'
import Notification from '../Notification'

import { HeaderContainer } from './HeaderStyles'

const Header = (props) => {
  console.log(props.notification)
  return (<HeaderContainer>
    <Nav user={props.user} handleLogin={props.handleLogin} handleLogout={props.handleLogout} handleRegister={props.handleRegister}/>
    <Notification message={props.notification[0]} status={props.notification[1]} />
  </HeaderContainer>)
}

const mapStateToProps = (state, ownProps) => {
  return {
    notification: state.notification,
    handleLogin: ownProps.handleLogin,
    handleLogout: ownProps.handleLogout,
    handleRegister: ownProps.handleRegister
  }
}

export default connect(mapStateToProps)(Header)